import React from 'react';
import { connect } from 'react-redux';
import {Table} from 'reactstrap';
import { getLoans } from 'actions/ManageLoans';


class manageLoans extends React.Component {

    componentDidMount(){
        this.props.getLoans();
    }


    render(){
        return (
            <div className="table-responsive-material">
                <Table>
                    <thead>
                    <tr>
                        <th className="border-top-0"></th>
                        <th className="border-top-0">Payment Terms</th>
                        <th className="border-top-0">Interest rate</th>
                        <th className="border-top-0">Amount</th>
                        <th className="border-top-0">Level</th>
                    </tr>
                    </thead>
                    <tbody>

                    {this.props.loans.loans.map((item) =>  {
                        return (
                            <tr key={item.id}>
                                <td></td>
                                <td>{item.payment_terms}</td>
                                <td>{item.interest_rate}</td>
                                <td>{item.amount}</td>
                                <td>{item.level}</td>
                            </tr>
                        );
                    })}
                    </tbody>
                </Table>
            </div>
        );
    }
}


const mapStateToProps = (state) => ({
   loans: state.manageLoans
});

export default connect(mapStateToProps, {getLoans})(manageLoans);


